<template>
  <div class="home page">
    <nav>
      证件制作
      <div class="right-side">
        <span @click="onBtnAdd">添加</span>
        <span @click="onCreate">生成</span>
      </div>
    </nav>
    <div class="c-topbox">
      <span>起始编号:</span>
      <Input
        :value="startCode"
        @on-change="setStartCode"
        class="c-startCode-text"
        type="number"
      />
    </div>
    <div class="content">
      <div class="c-item" v-for="(item, index) of list" :key="item.id">
        <div class="c-item-index">
          <span>{{ index + 1 }}</span>
        </div>
        <div @click="onViewImg(item, startCode + index)">
          <img class="c-item-avatar" :src="item.img" />
        </div>
        <div class="c-item-rightcontent">
          <ul>
            <li>
              <span>姓名:</span>&nbsp;<Input
                class="c-name-text"
                type="text"
                :value="item.name"
                @on-change="onNameInputChange($event, item.id)"
              />
            </li>
            <li>
              <span>编号:</span>&nbsp;<b>{{ startCode + index }}</b>
            </li>
          </ul>
          <div class="spacer"></div>
          <div class="c-btnbox">
            <Button type="primary" size="small" @click="onBtnEdit(item.id)"
              >重传头像</Button
            >
            <Button
              type="success"
              size="small"
              @click="onViewImg(item, startCode + index)"
              >查看</Button
            >
            <Button type="error" size="small" @click="onDelItem(item.id)"
              >删除</Button
            >
          </div>
        </div>
      </div>
    </div>
    <input
      type="file"
      ref="theFile"
      accept="image/jpeg,image/png"
      @change="onFileSelect"
      style="display:none;"
    />
    <img ref="cardBg" :src="cardBgSrc" style="display:none" />
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      cardBgSrc: require("../assets/bg.jpg"),
      // A4纸竖版, 9个证的坐标
      cardPos: [
        [212, 122],
        [922, 122],
        [1632, 122],
        [212, 1203],
        [922, 1203],
        [1632, 1203],
        [212, 2285],
        [922, 2285],
        [1632, 2285],
      ],
    };
  },
  mounted() {
    this.createCanvas();
  },
  methods: {
    createCanvas() {
      this.cvsBg = document.createElement("canvas");
      this.cvsBg.width = 651;
      this.cvsBg.height = 1004;
      this.cvsPaper = document.createElement("canvas");
      this.cvsPaper.width = 2516;
      this.cvsPaper.height = 3402;
    },
    onFileSelect(e) {
      //console.log(e.target.files)
      let img = e.target.files[0];
      let id = this.editId;
      this.$router.push({
        name: "Edit",
        params: {
          id,
          img,
        },
      });
      this.editId = null;
      e.target.value = "";
    },
    setStartCode(e) {
      console.log(e.target.value);
      this.$store.commit("setStartCode", +e.target.value);
    },
    onBtnAdd() {
      if (this.list.length >= 9) {
        this.$Modal.warning({
          title: "提示",
          content: "人数不能超过9个",
        });
        return;
      }
      this.$refs.theFile.click();
    },
    onBtnEdit(id) {
      this.editId = id;
      this.$refs.theFile.click();
    },
    onViewImg(item, code) {
      this.drawCard(item, code).then((img) => {
        this.$router.push({
          name: "Img",
          params: {
            img,
          },
        });
      });
    },
    onNameInputChange(e, id) {
      let o = this.getItemById(id);
      if (!o) return;
      o.name = e.target.value;
      this.$store.commit("editItem", o);
    },
    getItemById(id) {
      for (var i = 0; i < this.list.length; i++) {
        if (this.list[i].id == id) {
          return this.list[i];
        }
      }
      return null;
    },
    onDelItem(id) {
      this.$Modal.confirm({
        title: "提示",
        content: "是否删除?",
        onOk: () => {
          this.$store.commit("delItem", id);
        },
        onCancel: () => {
          console.log("cancel");
        },
      });
    },

    drawCard(item, code) {
      return new Promise((rs, rj) => {
        let ctx = this.cvsBg.getContext("2d");
        ctx.clearRect(0, 0, this.cvsBg.width, this.cvsBg.height);
        ctx.drawImage(this.$refs.cardBg, 0, 0);
        this.loadImage(item.img)
          .then((hd) => {
            ctx.drawImage(hd, 0, 0, hd.width, hd.height, 208, 265, 250, 360);
            // 392 750    392 824
            ctx.font = "50px '微软雅黑','宋体'";
            ctx.textAlign = "center";
            ctx.textBaseline = "hanging";
            ctx.fillStyle = "#545454";
            ctx.fillText(item.name, 392, 652);
            ctx.fillText(this.fillNumber(code), 392, 734);
            let data = this.cvsBg.toDataURL("image/jpeg", 1.0);
            return this.loadImage(data);
            // console.log(data)
            // let w = window.open("about:blank");
            // let i = document.createElement('img')
            // i.src = data;
            // w.document.body.appendChild(i)
          })
          .then((img) => {
            rs(img);
          });
      });
    },

    loadImage(src) {
      return new Promise((rs, rj) => {
        let img = new Image();
        img.src = src;
        img.onload = () => {
          rs(img);
        };
        img.onerror = () => {
          rj();
        };
      });
    },

    onCreate() {
      if (this.list.length > 9 || this.list.length == 0) {
        this.$Modal.warning({
          title: "提示",
          content: "人数不能超过9个或为0",
        });
        return;
      }
      this.$Modal.confirm({
        title: "提示",
        content: "生成以下" + this.list.length + "人的证件?",
        onOk: () => {
          this.doCreate();
        },
        onCancel: () => {
          console.log("cancel");
        },
      });
    },
    // doCreate() {
    //   let arr = [];
    //   for(let i = 0; i < this.list.length; i++) {
    //     let pm = this.drawCard(this.list[i], this.startCode + i);
    //     arr.push(pm);
    //   }
    //   let ctx = this.cvsPaper.getContext("2d");
    //   ctx.fillStyle = '#FFFFFF';
    //   ctx.fillRect(0, 0, this.cvsPaper.width, this.cvsPaper.height);
    //  如果使用Promise.all 会出现问题, 因为promise.all是这几个一起执行的,但是是在一个画布上画,会画重叠东西,所以用await比较好
    //   Promise.all(arr).then(resArr=>{
    //     console.log(resArr)
    //     for(let i = 0; i < resArr.length; i++) {
    //       ctx.drawImage(resArr[i], ...this.cardPos[i])
    //     }
    //   }).then(()=>{
    //     let data = this.cvsPaper.toDataURL("image/jpeg", 1.0);
    //     //console.log(data)
    //     //document.body.appendChild(this.cvsPaper)
    //     let w = window.open("about:blank");
    //     let pic = document.createElement('img')
    //     pic.src = data;
    //     w.document.body.appendChild(pic)
    //   })

    // },
    async doCreate() {
      let ctx = this.cvsPaper.getContext("2d");
      ctx.fillStyle = "#FFFFFF";
      ctx.fillRect(0, 0, this.cvsPaper.width, this.cvsPaper.height);
      for (let i = 0; i < this.list.length; i++) {
        let d = await this.drawCard(this.list[i], this.startCode + i);
        ctx.drawImage(d, ...this.cardPos[i]);
      }
      let data = this.cvsPaper.toDataURL("image/jpeg", 1.0);
      let img = await this.loadImage(data);
      this.$router.push({
        name: "Img",
        params: {
          img,
        },
      });
      //console.log(data)
      //document.body.appendChild(this.cvsPaper)
      // let w = window.open("about:blank");
      // let pic = document.createElement('img')
      // pic.src = data;
      // w.document.body.appendChild(pic)
    },
    fillNumber(n) {
      return n.toString().padStart(4, "0");
    },
  },

  computed: {
    ...mapState(["list", "startCode"]),
  },
};
</script>

